.Crystal {
  position: relative;
}
.Crystal .Center {
  background-color: #006329;
  height: 24px;
  width: 20px;
  position: relative;
  z-index: 0;
}
.Crystal .Center:after, .Crystal .Center:before {
  animation: CenterAfter 1s linear infinite;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  transform-origin: 100% 50%;
  width: 100%;
  z-index: 1;
}
.Crystal .Center:before {
  animation: CenterBefore 1s linear infinite;
  z-index: 2;
}
.Crystal .Point {
  clip-path: polygon(0 100%, 50% 0, 100% 100%);
  height: 8px;
  background-color: #01953e;
  z-index: 0;
}
.Crystal .Point:after, .Crystal .Point:before {
  animation: PointBeforeLight 1s linear infinite;
  content: "";
  clip-path: polygon(0 100%, 50% 0, 100% 100%);
  height: 8px;
  background-color: #01fb68;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transform-origin: 50% 0;
  z-index: 2;
}
.Crystal .Point:after {
  animation: PointAfterLight 1s linear infinite;
  z-index: 1;
  transform: scaleX(0.54) skew(51deg);
  background-color: #64fea3;
}
.Crystal .Point:last-child {
  transform: rotate(180deg) scaleX(-1);
  background-color: #003014;
}
.Crystal .Point:last-child:before {
  animation: PointBeforeDark 1s linear infinite;
}
.Crystal .Point:last-child:after {
  animation: PointAfterDark 1s linear infinite;
}

@keyframes CenterAfter {
  from {
    background-color: #01C853;
    transform: scaleX(0.5);
  }
  to {
    background-color: #006329;
    transform: scaleX(1);
  }
}
@keyframes CenterBefore {
  from {
    background-color: #31fe85;
    transform: scaleX(0);
  }
  to {
    background-color: #01C853;
    transform: scaleX(0.5);
  }
}
@keyframes PointBeforeLight {
  0% {
    background-color: #01fb68;
    transform: scaleX(0.54) skew(51deg);
  }
  50% {
    transform: scaleX(0.5);
  }
  100% {
    background-color: #01953e;
    transform: scaleX(0.54) skew(-51deg);
  }
}
@keyframes PointAfterLight {
  from {
    background-color: #64fea3;
  }
  to {
    background-color: #01fb68;
  }
}
@keyframes PointBeforeDark {
  0% {
    background-color: #01953e;
    transform: scaleX(0.54) skew(51deg);
  }
  50% {
    transform: scaleX(0.5);
  }
  100% {
    background-color: #003014;
    transform: scaleX(0.54) skew(-51deg);
  }
}
@keyframes PointAfterDark {
  from {
    background-color: #01fb68;
  }
  to {
    background-color: #01953e;
  }
}
.collection {
  padding-top: 100px;
  margin-bottom: 200px;
}
.collection__title {
  margin: 0;
  margin-bottom: 30px;
}

.filters {
  margin-bottom: 40px;
}

.filters__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.filters__item {
  font-family: var(--second-family);
  text-transform: uppercase;
  border-radius: 10px;
}

.filters__item:not(:last-child) {
  margin-right: 10px;
}

.filters__item.active span {
  background: linear-gradient(180deg, #FEE89A 18.23%, #F89EE0 83.33%);
  color: #2A2634;
  border: none;
}

.filters__link {
  display: inline-block;
  padding: 20px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.15);
  color: #fff;
  transition: background 0.4s cubic-bezier(0.25, 0.1, 0.25, 1), border-radius 0.7s ease;
}

.filters__link:hover {
  background: rgb(251, 204, 204);
  border-radius: 14px;
}

.filters__top {
  position: relative;
  padding-left: 64px;
  margin-bottom: 20px;
}

.filters__top::before {
  content: "";
  width: 48px;
  height: 48px;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 10px;
  background-image: url(../img/collect-icon.svg);
  background-repeat: no-repeat;
  background-size: cover;
  transition: background-image 0.2s ease;
}

.filters__link:hover .filters__top::before {
  background-image: url(../img/collect-icon-h.svg);
}

.filters__subtitle {
  font-family: var(--second-family);
  font-size: 18px;
  text-transform: uppercase;
  color: #fff;
  margin: 0;
  margin-bottom: 5px;
  transition: color 0.2s ease;
}

.filters__link:hover .filters__subtitle {
  color: #212121;
  transition: color 0.2s ease;
}

.filters__price {
  color: #C9C9C9;
}

.filters__link:hover .filters__price {
  color: #212121;
  transition: color 0.2s ease;
}

.filters__img-box {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 14px;
  grid-auto-flow: dense;
}

.filters__img {
  object-fit: cover;
  object-position: center;
  height: 100%;
  width: 100%;
  border-radius: 10px;
}

.filters__img:nth-child(1) {
  grid-column: 6 span;
  grid-row-end: span 2;
}

.filters__img:nth-child(2) {
  grid-column: 6 span;
  grid-row-end: span 1;
}

.filters__img:nth-child(3) {
  grid-column: 6 span;
  grid-row-end: span 1;
}

.content {
  height: auto;
}

.filters-card {
  margin: 10px 10px;
  width: 380px;
  height: 368px;
  float: left;
}

.filters-grid:after {
  content: "";
  display: block;
  clear: both;
}

.filters {
  width: 100%;
}