.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(149.3deg, #F35DED 65.14%, #F3B75D 89.45%);
  box-shadow: inset 0px 4px 33px rgba(255, 255, 255, 0.37);
  border-radius: 15px;
  font-family: var(--second-family);
  font-size: 16px;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 24px 44px;

  transition: color .3s ease, border-radius .3s ease;

    &:hover {
      background: linear-gradient(149.3deg, #F3B75D 67.79%, #F35DED 89.45%);
      color: #2A2634;
      border-radius: 20px;
      transition: color .2s ease, border-radius .7s ease;
    }
}
