.art {

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 40px;
  }

  &__title-box {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: nowrap;
    font-family: var(--second-family);
  }

  &__title {
    margin: 0;
  }

  &__counter {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 5px;
    background-color: #F46AEE;
    padding: 2px;
    min-height: 30px;
    min-width: 30px;
    border-radius: 3px;
    color: #fff;
    font-size: 14px;
    transform: translateY(-10px);
  }
}


.art__btn-box {
  font-family: var(--second-family);
  display: block;
  cursor: pointer;
  color: #A08094;
  text-transform: uppercase;
  position: relative;
  text-decoration: none;
  font-weight: 400;
  border-radius: 10px;
  overflow: hidden;
  padding: 1px;
  isolation: isolate;
  transition: color .3s ease;
}

.art__btn-box::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 400%;
  height: 100%;
  background: linear-gradient(115deg,#f148ec,#F3B75D,#fecc82,#F35DED,#f31cec);
  background-size: 25% 100%;
  animation: an-at-keyframe-css-at-rule-that-translates-via-the-transform-property-the-background-by-negative-25-percent-of-its-width-so-that-it-gives-a-nice-border-animation_-We-use-the-translate-property-to-have-a-nice-transition-so-it_s-not-a-jerk-of-a-start-or-stop .75s linear infinite;
  animation-play-state: paused;
  translate: -5% 0%;
  transition: translate 0.25s ease-out;
}

.art__btn-box:hover {
  color: #fff;
}

.art__btn-box:hover::before {
  animation-play-state: running;
  transition-duration: 0.75s;
  translate: 0% 0%;
}

@keyframes an-at-keyframe-css-at-rule-that-translates-via-the-transform-property-the-background-by-negative-25-percent-of-its-width-so-that-it-gives-a-nice-border-animation_-We-use-the-translate-property-to-have-a-nice-transition-so-it_s-not-a-jerk-of-a-start-or-stop {
  to {
    transform: translateX(-25%);
  }
}

.art__btn-box span {
  position: relative;
  display: block;
  padding: 12px 40px;
  font-size: 14px;
  background: #2B2735;
  border-radius: 10px;
  height: 100%;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
}

.card__box {
}

.card {
  position: relative;
  display: flex;
  align-items: center;
  width: 384px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  padding: 26px;
  overflow: hidden;
  border-radius: 15px;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);

  background: rgba(255, 255, 255, 0.15);
}

.card__top {
  border-radius: 20px;
  height: 381px;
  width: 384px;
  margin-bottom: 10px;
  overflow: hidden;

  border: 2px solid transparent;
  animation: rainbow 5s ease infinite;
}

@keyframes rainbow {
  0% {
    border-color: #f148ec;
  }
  25% {
    border-color: #F3B75D;
  }
  50% {
    border-color: #fecc82;
  }
  75% {
    border-color: #F35DED;
  }
  100% {
    border-color: #f31cec;
  }
}


.card__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #fff;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
}

.card__title {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 24px;
  font-family: var(--second-family);
}

.card__text {
  margin: 0;
  margin-bottom: 20px;
  max-width: 274px;
  font-size: 15px;
  margin-right: 10px;
}

.card__inner-box {
  display: flex;
  justify-content: space-between;
}

.card__right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  padding-bottom: 6px;
  max-width: 60px;
  border-radius: 10px;
  color: #353034;
  text-align: center;
  font-size: 14px;
  line-height: 1.1;
  text-transform: uppercase;
  font-family: var(--second-family);
  background: linear-gradient(180deg, #FFF3CA 0%, #F9B3CC 100%);
  transition: background .8s ease;
}

.card:hover .card__right {
  background: #f1f1f1;
}

.card__price {
  margin: 0;
  margin-top: 6px;
}

.card__btn {
  font-family: var(--second-family);
  display: block;
  position: relative;
  mix-blend-mode: color-dodge;
  perspective: 500px;
  transform-style: preserve-3d;
  font-size: 12px;
  text-transform: uppercase;
  cursor: pointer;
  color: #D8C5D1;
  padding: 13px 30px;
  border-radius: 10px;
  border: 1px solid #9876ea;
}


.card__btn:before,.card__btn:after {
  --z: 0px;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  opacity: 0;
  mix-blend-mode: inherit;
  border-radius: inherit;
  transform-style: preserve-3d;
  transform: translate3d(calc(var(--z) * 0px), calc(var(--z) * 0px), calc(var(--z) * 0px));
 }

.card__btn span {
  mix-blend-mode: none;
  display: block;
 }

.card__btn:after {
  background-color: #5D00FF;
 }

.card__btn:before {
  background-color: #FF1731;
 }

.card__btn:hover {
  background-color: #FFF65B;
  transition: background 0.3s 0.1s;
 }

.card__btn:hover:before {
  --z: 0.04;
  animation: translateWobble 2.2s ease forwards;
 }

.card__btn:hover:after {
  --z: -0.06;
  animation: translateWobble 2.2s ease forwards;
 }

 @keyframes rotateAngle {
  0% {
   transform: rotateY(0deg) rotateX(10deg);
   -webkit-animation-timing-function: cubic-bezier(0.61, 1, 0.88, 1);
   animation-timing-function: cubic-bezier(0.61, 1, 0.88, 1);
  }

  25% {
   transform: rotateY(20deg) rotateX(10deg);
  }

  50% {
   transform: rotateY(0deg) rotateX(10deg);
   -webkit-animation-timing-function: cubic-bezier(0.61, 1, 0.88, 1);
   animation-timing-function: cubic-bezier(0.61, 1, 0.88, 1);
  }

  75% {
   transform: rotateY(-20deg) rotateX(10deg);
  }

  100% {
   transform: rotateY(0deg) rotateX(10deg);
  }
 }

 @keyframes translateWobble {
  0% {
   opacity: 0;
   transform: translate3d(calc(var(--z) * 0px), calc(var(--z) * 0px), calc(var(--z) * 0px));
  }

  16% {
   transform: translate3d(calc(var(--z) * 160px), calc(var(--z) * 160px), calc(var(--z) * 160px));
  }

  28% {
   opacity: 1;
   transform: translate3d(calc(var(--z) * 70px), calc(var(--z) * 70px), calc(var(--z) * 70px));
  }

  44% {
   transform: translate3d(calc(var(--z) * 130px), calc(var(--z) * 130px), calc(var(--z) * 130px));
  }

  59% {
   transform: translate3d(calc(var(--z) * 85px), calc(var(--z) * 85px), calc(var(--z) * 85px));
  }

  73% {
   transform: translate3d(calc(var(--z) * 110px), calc(var(--z) * 110px), calc(var(--z) * 110px));
  }

  88% {
   opacity: 1;
   transform: translate3d(calc(var(--z) * 90px), calc(var(--z) * 90px), calc(var(--z) * 90px));
  }

  100% {
   opacity: 1;
   transform: translate3d(calc(var(--z) * 100px), calc(var(--z) * 100px), calc(var(--z) * 100px));
  }
 }

.card::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 6px;
  height: 100%;
  background: linear-gradient(-45deg, #f89b29 0%, #ff0f7b 100% );
  z-index: -1;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
}

.card:hover::before {
  width: 100%;
}

.card:hover {
  box-shadow: none;
}

.card:hover .card__btn {
  color: #212121;
  background: #e8e8e8;
}

.card__btn:hover {
  outline: 2px solid #e8e8e8;
  background-color: transparent;
  color: #e8e8e8;
}

.card__btn:active {
  box-shadow: none;
}






$size: 20px;
$width: $size;
$height: $size * 2;
$speed: 1s;
$timing: linear;
$colorSpread: 20%;
$color: #01C853;
$colorDark: darken($color, $colorSpread);
$colorLight: lighten($color, $colorSpread);


// ----Crystal----

.Crystal {
  position: relative;

  .Center {
	  background-color: $colorDark;
	  height: $height * 0.6;
	  width: $width;
    position: relative;
    z-index: 0;

    &:after,
    &:before {
      animation: CenterAfter $speed $timing infinite;
      content: '';
      display: block;
      height: 100%;
      position: absolute;
      right: 0;
      top: 0;
      transform-origin: 100% 50%;
      width: 100%;
      z-index: 1;
    }

    &:before {
      animation: CenterBefore $speed $timing infinite;
      z-index: 2;
    }
  }

  .Point {
    clip-path: polygon(0 100%, 50% 0, 100% 100%);
    height: $height * 0.2;
    background-color: lighten($colorDark, 10%);
    z-index: 0;

    &:after,
    &:before {
      animation: PointBeforeLight $speed $timing infinite;
      content: '';
      clip-path: polygon(0 100%, 50% 0, 100% 100%);
      height: $height * 0.2;
      background-color: lighten($color, 10%);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      transform-origin: 50% 0;
      z-index: 2;
    }

    &:after {
      animation: PointAfterLight $speed $timing infinite;
      z-index: 1;
      transform: scaleX(0.54) skew(51deg);
      background-color: lighten($colorLight, 10%);
    }
  }

  .Point:last-child {
    transform: rotate(180deg) scaleX(-1);
    background-color: darken($colorDark, 10%);

    &:before {
      animation: PointBeforeDark $speed $timing infinite;
    }

    &:after {
      animation: PointAfterDark $speed $timing infinite;
    }
  }
}

// ----Animations----
@keyframes CenterAfter {
  from {
    background-color: $color;
    transform: scaleX(0.5);
  }

  to {
    background-color: $colorDark;
    transform: scaleX(1);
  }
}

@keyframes CenterBefore {
  from {
    background-color: $colorLight;
    transform: scaleX(0);
  }

  to {
    background-color: $color;
    transform: scaleX(0.5);
  }
}

@keyframes PointBeforeLight {
  0% {
    background-color: lighten($color, 10%);
    transform: scaleX(0.54) skew(51deg);
  }

  50% {
    transform: scaleX(0.5);
  }

  100% {
    background-color: lighten($colorDark, 10%);
    transform: scaleX(0.54) skew(-51deg);
  }
}

@keyframes PointAfterLight {
  from {
    background-color: lighten($colorLight, 10%);
  }

  to {
    background-color: lighten($color, 10%);
  }
}

@keyframes PointBeforeDark {
  0% {
    background-color: darken($color, 10%);
    transform: scaleX(0.54) skew(51deg);
  }

  50% {
    transform: scaleX(0.5);
  }

  100% {
    background-color: darken($colorDark, 10%);
    transform: scaleX(0.54) skew(-51deg);
  }
}

@keyframes PointAfterDark {
  from {
    background-color: darken($colorLight, 10%);
  }

  to {
    background-color: darken($color, 10%);
  }
}
