.hero {
  padding-top: 85px;
  padding-bottom: 50px;

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: var(--offset);
  }

  &__content {
    grid-column: 6 span;
    margin-top: 100px;
  }

  &__video {
    width: 100%;
    height: 100%;
    grid-column: 6 span;
  }

  &__subtitle {
    display: inline-block;
    margin: 0;
    margin-bottom: 50px;
    font-family: var(--second-family);
    font-size: 16px;
    text-transform: uppercase;
    padding: 10px;
    background-color: #3E3A49;
    border-radius: 5px;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-family: var(--second-family);
    font-size: 48px;
  }

  &__descr {
    margin: 0;
    font-size: 20px;
  }

  &__btn {
    margin-top: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(149.3deg, #F35DED 65.14%, #F3B75D 89.45%);
    box-shadow: inset 0px 4px 33px rgba(255, 255, 255, 0.37);
    border-radius: 15px;
    font-family: var(--second-family);
    font-size: 16px;
    text-transform: uppercase;
    color: #FFFFFF;
    padding: 22px 76px 22px 52px;
    position: relative;
    transition: border-radius .3s ease;

    &::after {
      content: '';
      display: inline-block;
      position: absolute;
      left: 215px;
      width: 15px;
      height: 15px;
      background-image: url(../img/btn-arrow.svg);
      transition: transform .3s ease;
    }

    &:hover::after {
      transform: translateX(5px);
    }

    &:hover {
      background: linear-gradient(149.3deg, #F3B75D 67.79%, #F35DED 89.45%);
      border-radius: 20px;
      transition: border-radius .7s ease;
    }
  }
}


.hero__subtitle--animate {
  --col1: rgba(228, 19, 141, 0.925);
  --col2: rgb(255, 179, 80);
  font-weight: 600;
  perspective: 800px;
  position: relative;
}

.hero__subtitle--animate::after,
.hero__subtitle--animate::before,
.hero__subtitle--animate span::after,
.hero__subtitle--animate span::before {
  perspective: 800px;
  animation: anim 2s ease-in-out infinite, dotMove 10s ease-out alternate infinite, move 10s linear infinite 1s;
  ;
  content: '‚óè';
  color: var(--col1);
  position: absolute;
  translate: -60px 500px;
  width: 5px;
  height: 5px;
}

.hero__subtitle--animate::before {
  animation-delay: 3s;
  color: var(--col1);
}

.hero__subtitle--animate span::before {
  color: var(--col2);
  animation-delay: 2s;
}

.hero__subtitle--animate span::after {
  color: var(--col2);
}

.hero__subtitle--animate span {
  animation: anim 5s ease infinite, move 10s linear infinite 1s;
  color: transparent;
  background-image: linear-gradient(90deg,
  var(--col1) 0%,
  var(--col2) 100%);
  background-clip: text;
  background-size: 100%;
  background-repeat: no-repeat;
  transform: skew(5deg, -5deg);
  -webkit-background-clip: text;
  position: relative;
}

@keyframes anim {
  0%, 100% {
    text-shadow: 2px 0px 2px rgba(179, 158, 158, .5);
  }

  50% {
    background-size: 0%;
    background-position-x: left;
    text-shadow: 2px 10px 6px rgba(179, 158, 158, 1);
  }
}

@keyframes move {
  50% {
    translate: 0px 0px;
    rotate: x 60deg;
    transform: skew(-5deg, 5deg);
  }
}

@keyframes dotMove {
  0%, 100% {
    translate: -60px 300px;
  }

  50% {
    translate: 160px -250px;
    scale: .5;
    opacity: .85;
  }
}
